@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
@import "components/_grid.scss";

._transition{
  transition: all .125s;
}

._scale-img{
  overflow: hidden;
  &:hover{
    img{
      transform: scale(1.05);
    }
  }
  img{
    transition: all .6s;
  }
}

.text h2,
.text h3,
.text h4,
.text h5,
.text h6,
._animate-block{
  opacity: 0;
  transform: translateY(30px);
  transition: all .8s, color .125s;
  &._animate-block--active{
    opacity: 1;
    transform: translateY(0);
  }
}

.heading-line{
  display: block;
  line-height: inherit;
  overflow: hidden;
  &__inner{
    display: block;
    transition: all .6s, color .125s;
    transition-timing-function: ease-in-out;
    transform: translateY(110%);
    will-change: transform;
  }
  &:nth-child(2) .heading-line__inner{
    transition-delay: .3s;
    transition-timing-function: ease-in;
  }
  &:nth-child(3) .heading-line__inner{
    transition-delay: .6s;
    transition-timing-function: ease;
  }
  &._animate-block--active{
    .heading-line__inner{
      transform: translateY(0);
    }
  }
}


@media screen and (max-width: 992px){
  .heading-line{
    display: inline;
    opacity: 1;
    transform: translateY(0);
    &__inner{
      display: inline;
    }
  }
}
