@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 2.5em;
    height: 4em;
    border-radius: 0.375em;
    font-weight: 500;
    position: relative;
    overflow: hidden;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: .9375em;
        line-height: 120%;
        &:not(._animate-span){
            position: relative;
            z-index: 2;
        }
    }
    svg,
    img{
        margin-left: .75em;
        width: .6875em;
        position: relative;
        z-index: 2;
    }
    &._small{
        padding: 0 1.875em;
        height: 3.1875em;
    }
    ._animate-span{
        position: absolute;
        display: block;
        width: 0;
        height: 0;
        border-radius: 50%;
        transition: width 0.4s ease-in-out, height 0.4s ease-in-out;
        transform: translate(-50%, -50%);
        //z-index: -1;
    }
    &:hover{
        ._animate-span{
            width: 330px;
            height: 330px;
        }
    }
}

.m-btn-white-outline,
.m-btn-blue{

}

.m-btn-blue{
    background-color: $primary;
    color: #fff;
    ._animate-span{
        background-color: $black;
    }
    &:hover{
        color: #fff;
    }
    &:disabled{
        background: rgba(88, 94, 102, 0.20);
    }
    &._black{
        &:hover{
            color: $black;
            path{
                stroke: $black;
            }
        }
        ._animate-span{
            background-color: #fff;
        }
    }
}

.m-btn-blue-outline{
    color: $primary;
    border: 2px solid $primary;
    ._animate-span{
        background-color: $primary;
    }
    &:hover{
        color: #fff;
        path{
            stroke: #fff;
        }
    }
    &:disabled{
        border-color: rgba($gray, 0.20);
        color: rgba($gray, 0.20);
        path{
            stroke: rgba($gray, 0.20);
        }
    }
}

.m-btn-white-outline{
    border: 2px solid #fff;
    color: #fff;
    &:hover{
        border-color: $primary;
        color: #fff;
    }
    ._animate-span{
        background-color: $primary;
    }
}

.m-btn-white{
    background-color: #fff;
    color: $primary;
    ._animate-span{
        background-color: $black;
    }
    &:hover{
        color: #fff;
        path{
            stroke: #fff;
        }
    }
    &:disabled{
        background: rgba(88, 94, 102, 0.20);
        color: #fff;
        path{
            stroke: #fff;
        }
    }
}

.white-link{
    &:hover{
        color: #fff;
    }
}


@media screen and (max-width: 992px){
    .m-btn{
        height: 3.75em;
        padding: 0 1.875em;
        &._small{
            padding: 0 1.25em;
            height: 2.75em;
        }
    }
}


@media screen and (max-width: 576px){
    .m-btn{
        height: 2.9375em;
        svg,
        img{
            width: .5em;
            margin-left: .5em;
        }
        span{
            font-size: .875em;
        }
    }
}
