.conf-page{
  &__top{
    max-width: 52em;
  }
  &__text{
    margin-top: 3.125em;
    a{
      color: $primary;
      &:hover{
        color: $orange;
      }
    }
  }
  .text{
    ul{
      padding-left: 0;
      li{
        padding-left: 1.5em;
        &:before{
          content: "";
          position: absolute;
          top: 0;
          left: 0;
          font-size: 1.125em;
          background: unset;
          padding-left: .625em;
        }
      }
    }
  }
}

.conf-info{
  padding: 2.5em;
  background-color: $bgGray;
  border-radius: 1.25em;
  margin-top: 3.125em;
}


@media screen and (max-width: 992px){
  .conf-info{
    margin-top: 2.5em;
    padding: 1.875em;
  }
}


@media screen and (max-width: 576px){
  .conf-info{
    margin-top: 1.875em;
    padding: 1.25em;
  }
}
